@import "../Variables.scss";

table {
  padding: 0 1%;
  th {
    float: left;
    color: $c1;
  }
  td {
    font-size: 120%;
  }
}

.blogCont {
  width: 70%;
  background-color: $c5;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin: 100px auto;
  @media only screen and (max-width: 1200px) {
    width: 98%;
    margin: auto;
  }
  .blogBar {
    width: 100%;
    background-color: $c3;
    background-image: linear-gradient(270deg, $c3 0%, $c6 45%, $c6 100%);
    height: 60px;
    border-top-left-radius: 11px;
    border-top-right-radius: 11px;
    font-size: 120%;
    h1 {
      padding-top: 15px;
    }
  }
  .blogCont1 {
    scrollbar-face-color: $c3;
    scrollbar-track-color: $c1;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: $c3 $c1;
    color: $c1;
    width: 100%;
    &::-webkit-scrollbar {
      width: 10px;
    }
    &::-webkit-scrollbar-track {
      background: $c1;
    }
    &::-webkit-scrollbar-thumb {
      background: $c3;
    }
    &::-webkit-scrollbar-thumb:hover {
      background: $c7;
    }
  }
}

.blogs {
  color: $c1;
}

.blogh {
  width: 100%;
  background-color: $c3;
  background-image: linear-gradient(270deg, $c3 0%, $c6 45%, $c6 100%);
  height: 60px;
  border-top-left-radius: 11px;
  border-top-right-radius: 11px;
  font-size: 180%;
  h2 {
    padding-top: 12.5px;
    font-family: $font1;
    color: $c1;
    float: right;
    margin: 0;
    padding-right: 20px;
    font-size: 60%;
  }
}

.float {
  float: right;
  width: 40%;
  padding-left: 60% !important;
  @media only screen and (max-width: 1200px) {
    width: 100%;
  }
}

.form {
  width: 100%;
  background-color: $c5;
  color: $c1;
  overflow: hidden;
  .blogsi {
    transform: scale(1.6);
    padding: 15px;
    float: left;
  }
}

.form0 {
  width: 100%;
  margin: auto;
  background-color: $c5;
  color: $c1;
  overflow: hidden;
}

.custom-file-input[type="file"] {
  border: 1px solid $c9;
  border-radius: 3px;
  padding: 5px 8px;
  cursor: pointer;
  width: 35%;
  @media only screen and (max-width: 1200px) {
    width: 90%;
  }
  &::-ms-browse {
    background-color: $c1;
    margin: 0px;
    padding: 0px;
    border: 0px;
  }
  &::-webkit-file-upload-button {
    background-color: $c1;
    margin: 0px;
    padding: 0px;
    border: 0px;
  }
}

.MuiFilledInput-multiline {
  scrollbar-face-color: $c3;
  scrollbar-track-color: $c1;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-color: $c3 $c1;
  &::-webkit-scrollbar {
    width: 10px;
  }
  &::-webkit-scrollbar-track {
    background: $c1;
  }
  &::-webkit-scrollbar-thumb {
    background: $c3;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: $c7;
  }
}
